
.section a {
  color: #000;
}
.section__image {
  padding: 0 15px;
}
.section__subtitle  {
  padding: 0 15px 10px;
}
.section__title {
  font-size: $font-x-large;
  padding: 15px 15px 0;
}

.section-list-nav {
  padding: 10px 15px;
  text-transform: uppercase;
  font-weight: bold;
  a { 
    color: #000;
  }
}
.section-list-nav__item {
  display: inline;
  a { 
    color: #000;
    padding-right: 10px;
    text-decoration: underline;
  }
}

.section-list-nav__item--arrow-right {
  &:after {
    margin: 0 3px 0 -3px;
    font-size: $font-x-small;
     @media only screen and (min-width: 320px) { 
      content: ">";
    }
    @media only screen and (min-width: 490px) {
      content: "❯";
    }
  }
}
.section-list-nav__item--subsection {
  &:before {
    margin: 0 3px 0 -8px;
    font-size: $font-x-small;
     @media only screen and (min-width: 320px) { 
        content: ">";
      }
      @media only screen and (min-width: 490px) {
        content: "❯";
      }
  }
}

.section-list__title {
  text-transform: uppercase;
  padding: 10px 15px;
  border-bottom: solid 1px #e4e4e4;
}
.section-list__title--homepage {
  padding: 10px 0 0;
  border-bottom: none;
}
.section-list__more {
  display: block;
  text-transform: uppercase;
  padding: 10px 15px;
  font-weight: bold;
  text-decoration: underline;
}

